<template>
    <div class="mt-4">
        <select @change="onChange" v-model="selected" class="form-select">
            <option value="0" disabled>Select Country</option>
            <option v-for="country in countries" :key="country.ID" :value="country.ID">{{country.Country}}</option>
        </select>
    </div>
</template>

<script>
    export default {
        name: 'FormBox',
        data() {
            return {
                selected: '0'
            }
        },
        props: [
            'countries'
        ],
        methods: {
            onChange() {
                const selectedCountry = this.countries.find(country => country.ID == this.selected);
                // console.log(selectedCountry);
                this.$emit('on-change', selectedCountry)
            }
        },
    }
</script>

