<template>
  <div v-if="!loading" class="col-12 col-md-6">
    <TitleBox :title="title" :time="time" />
    <CaseDeath :stats="stats" />
  </div>

  <div v-else class="col-12 col-md-6">
    <h5 class="text-center">Fetching Data..</h5>
    <img src="../assets/Loading_icon.gif" class="w-25 d-block mx-auto" alt="loading">
  </div>

  <FormBox :countries="countries" @on-change="updateData" />
  <button v-if="title != 'Global'" @click="clearCountry" class="btn btn-primary w-auto mt-4 mx-auto">Clear Country</button>
</template>

<script>
// @ is an alias to /src
import TitleBox from '@/components/TitleBox.vue'
import CaseDeath from '@/components/CaseDeath.vue'
import FormBox from '@/components/FormBox.vue'

export default {
  name: 'HomeView',
  data() {
    return {
      title: 'Global',
      countries: [],
      stats: {},
      time: '',
      loading: true
    }
  },
  components: {
    TitleBox, CaseDeath ,FormBox
  },
  methods: {
    async fetchData() {
      const res = await fetch('https://api.covid19api.com/summary');
      const data = await res.json();

      return data;
    },
    updateData(data) {
      // console.log(data);
      this.title = data.Country;
      this.stats = data;
    },
    async clearCountry() {
      this.loading = true;
      const data = await this.fetchData();
      this.title = 'Global',
      this.stats = data.Global;
      this.loading = false;
    }
  },
  async created() {
    const data = await this.fetchData();

    // console.log(data);
    this.loading = false;
    this.time = data.Date;
    this.stats = data.Global;
    this.countries = data.Countries;
  }
}
</script>
