<template>
    <div class="row text-dark g-md-3">
        <div class="col-12 col-md-6 mb-3 mb-md-0">
            <div class="card">
                <div class="card-body text-center">
                    <h5 class="text-primary">Cases</h5>
                    <p><span class="fw-bold">News: </span>{{numberWithCommas(stats.NewConfirmed)}}</p>
                    <p><span class="fw-bold">Total: </span>{{numberWithCommas(stats.TotalConfirmed)}}</p>
                </div>
            </div>
        </div>
        <div class="col-12 col-md-6">
            <div class="card">
                <div class="card-body text-center">
                    <h5 class="text-primary">Death</h5>
                    <p><span class="fw-bold">News: </span>{{numberWithCommas(stats.NewDeaths)}}</p>
                    <p><span class="fw-bold">Total: </span>{{numberWithCommas(stats.TotalDeaths)}}</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'CaseDeath',
        props: [
            'stats'
        ],
        methods: {
            numberWithCommas(x) {
                return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
            }
        }
    }
</script>

