<template>
    <div class="text-center">
        <h5>{{title}}</h5>
        <p>{{timestamp}}</p>
    </div>
</template>

<script>
    import moment from 'moment'

    export default {
        name: 'TitleBox',
        props: [
            'title', 'time'
        ],
        computed: {
            timestamp: function() {
                return moment(this.time).format('MMM-DD-YYYY, h:mm:ss a');
            }
        }
    }
</script>

